@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,700&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: montserrat, sans-serif;
}

body {
    font-family: sans-serif;
    line-height: 1.4;
}

h1 {
    font-weight: 700;
    color: #286090;
}

a {
    font-weight: 400;
    color: #286090;
    text-decoration: none;    
}

.visibleClass {
    opacity: 100;
}

.invisibleClass {
    opacity: 0;
    display: none !important;
}

.headerStyle {
    background: #e9e9e9;
    color: #151515;
    text-align: center;
    padding: 15px;
}

.headerLinkStyle {
    color: #2a2a2a;
    text-decoration: none;
}

.explainpageLink {
    text-decoration: none;
    font-size: 1.5rem;
    display: block;
    padding: 1rem;
    margin: .5rem 0;
    border-radius: 1rem;
    background-color: #286090;
    color: #fff;
}

.infopageWrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 75%;
    margin: 1rem auto;
}

.infopageWrap p {
    margin: .5rem 0;
}

.explainpageWrap ol {
    padding-left: 1rem;
    width: 100%;
}

.explainpageWrap li {
    margin: 1rem 0;
}

.explainpageWrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 75%;
    margin: 1rem auto;
}

.explainpageWrap p {
    margin: .5rem 0;
}

.singleObject {
    background-color: #f6f6f6;
    padding: .75rem;
    width: 50%;
    margin: 1rem;
    border-radius: 1rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.singleObject p:first-of-type {
    display: block;
    width: 100%;
    text-align: center;
}

.singleObject p:last-of-type {
    display: block;
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
    margin: .5% 0 .5% 0;
}

.singleObject button {
    display: block;
    width: 50%;
    margin: .5% 25% .5% 25%;
    text-align: center;
}

.singleObject a {
    display: block;
    width: 50%;
    margin: .5% 25% .5% 25%;
    text-align: center;
}

.singleObject button {
    display: block;
    border-radius: 1rem;
    border: solid 2px #e4e4e4;
    background:#fff;
    padding: 1em;
}

.singleObject button:hover {
    background: #cce5cc;
    cursor: pointer;
}

.singleObject button:disabled {
    background: #f6f6f6;
    opacity: .5;
}

.singleObjectPic {
    height: 200px;
    max-width: 95%;
}

.nextButtonWrap {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    width: 100%;
}

.messageWrap {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    width: 100%;
}

.messageWrap p {
    font-weight: 600;
}

.nextButtonWrap button {
    padding: 1rem;
    margin: .5rem;
    min-width: 20%;
    display: block;
    border-radius: 1rem;
    border: solid 2px #e4e4e4;
    background:#fff;
}

.nextButtonWrap button:disabled {
    opacity: .5;
}

.nextButtonWrap button:hover {
    cursor: pointer;
}

.scoringWrap {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    width: 100%;
}

.scoringWrap div {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 25%;
    margin: .5rem;
    border-radius: .5rem;
}

.scoringWrap div:last-of-type {
    background-color: #ffe557;
}

.scoringWrap div:first-of-type {
    background-color: #d0ff8b;
}

.scoringWrap div p {
    width: 100%;
    text-align: center;
}

.objectsWrap {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    height: 400px;
}

.container {
    background-color: #fff;
}

